<template>
  <div class="alert_wrap">
    <div class="alert-main" v-for="item in notices" :key="item.name">
      <div class="alert-content">{{ item.content }}</div>
    </div>
  </div>
</template>
<script>
let seed = 0;
function getUuid() {
  return `alert_${seed++}`;
}
export default {
  data() {
    return {
      notices: [],
      hasToast: false
    };
  },
  methods: {
     add(notice) {
      // await this.dealLastToast();
      // console.log("[log]===> await");
      // this.notices.shift();

      const uuid = getUuid();
      let _notice = Object.assign({name: uuid}, notice);
      this.notices.push(_notice);
      const {duration} = _notice;
      setTimeout(() => {
        this.remove(_notice.name);
      }, duration);
    },
    remove(name) {
      // console.log("[log]===>dop dom", name);
      const notices = this.notices;
      for (let i = 0; i < notices.length; i++) {
        if (notices[i].name === name) {
          this.notices.splice(i, 1);
          break;
        }
      }
    },
    dealLastToast() {
      return new Promise((resolve, reject) => {
        this.hasToast = true;
        setTimeout(() => {
          this.hasToast = false;
          console.log("[log]===> remove Last");
          this.notices.unshift();
          resolve(true);
        }, 1000);
      });
    }
  }
};
</script>
<style lang="less" scoped>
.alert_wrap {
  position: fixed;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  pointer-events: none;
}
.alert-content {
  display: block;
  position: absolute;
  left: 50%;
  bottom: 5rem;
  transform: translate3d(-50%, 0, 0) scale(1);
  // transform: translate(-50%, 1rem) scale(0.9);

  padding: 0.1rem 0.1rem;
  font-size: 0.25rem;
  background: rgba(0, 0, 0, 0.8);
  color: white;
  border-radius: 0.05rem;
  box-shadow: 0 0 0.05rem red;
  animation: ani 0.2s ease-out both;
}

@keyframes ani {
  0% {
    transform: translate3d(-50%, 1rem, 0) scale(0.9);
    // transform: translate(-50%, 1rem) scale(0.9);

    opacity: 0;
  }
  100% {
    transform: translate3d(-50%, 0, 0) scale(1);
    // transform: translate(-50%, 0) scale(1);
    opacity: 1;
  }
}
</style>
