import Vue from "vue";
import AlertCom from "./alert.vue";

let Instance;
let alert;

// 向Alert组件传递data数据
export const Alert = (options) => {
  if (typeof options === "string") {
    options = {
      content: options,
      duration: 1500,
    };
  }
  if (!Instance) {
    Instance = new Vue({
      render(h) {
        return h(AlertCom, {
          props: options,
        });
      },
    });
    const comp = Instance.$mount();
    document.body.appendChild(comp.$el);
    alert = Instance.$children[0];
  }
  alert.add(options);
};
